html, body {
  font-family: 'Roboto', sans-serif;
  padding: 0;
  margin: 0;
}

#root {
  height: 100vh;
}

* {
  box-sizing: border-box;
}

.joinOuterContainer {
  display: flex;
  justify-content: center;
  text-align: center;
  min-height: 100%;
  align-items: center;
  background-color: #1A1A1D;
}

.joinInnerContainer {
  max-width: 30%;
  min-width: 360px;
}

.room-list-container {
  max-width: 34%;
  min-width: 480px;
  min-height: 250px;
  margin-left: 40px;
  max-height: 20vh;
}

.room-list {
  display: flex;
  overflow-y: auto;
}

.room-list::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  background-color: #F5F5F5;
}

.room-list::-webkit-scrollbar
{
  width: 12px;
  background-color: #F5F5F5;
}

.room-list::-webkit-scrollbar-thumb
{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background-color: #D62929;
}

.room-list-column {
  flex: 50%;
  padding-left: 5px;
}

.joinInput {
  border-radius: 0;
  padding: 15px 20px;
  width: 100%;
}

.heading {
  color: white;
  font-size: 2em;
  border-bottom: 2px solid white;
  padding-bottom: 7px;
  margin-top: 0;
}

.heading2 {
  color: white;
  font-size: 2em;
  padding-bottom: 10px;
  border-bottom: 2px solid white;
  margin-top: 48px;
}

.room-count-heading {
  color: white;
  text-align: left;
}


.button {
    color: #fff !important;
    text-transform: uppercase;
    text-decoration: none;
    background: #e63946;
    padding: 20px;
    border-radius: 5px;
    display: inline-block;
    border: none;
    width: 100%;
}

.mt-20 {
  margin-top: 20px;
}

.display-room-name {
  color: white;
  text-align: left;
  word-wrap: break-word;
}

.display-room-count {
  color: white;
  text-align: left;
  word-wrap: break-word;
  padding-left: 2px;
}

.room-name-heading {
  color: white;
  text-align: left;
}

@media (max-width: 640px) {
  .joinOuterContainer {
    height: 100%;
    display: flex;
    flex-wrap: wrap;
  }

  .joinInnerContainer {
    width: 100%;
    flex: 50%;
  }

  .room-list-container {
    flex: 50%;
    max-width: 30%;
    min-width: 300px;
    margin-left: -20px;
  }

  #logo {
    margin-left: -22px;
  }

  .room-list {
    height: 120px;
    width: 340px;
  }

  .heading2 {
    margin-top: 10px;
  }
}

button:focus {
  outline: 0;
}