.form {
  display: flex;
  border-top: 2px solid #D3D3D3;
  height: 50px;
}

.input {
  border: none;
  border-bottom-left-radius: 10px;
  padding: 3%;
  width: 80%;
  font-size: 1.2em;
  height: 100%;
}

input:focus, textarea:focus, select:focus{
  outline: none;
}

.sendButton {
  height: 100%;
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #e63946;
  padding: 3%;
  display: inline-block;
  border: none;
  width: 20%;
  border-bottom-right-radius: 8px;
}

.sendButton:hover {
  cursor: pointer;
}